import {
  BILLING_CART_ADDRESS_FRAGMENT,
  SHIPPING_CART_ADDRESS_FRAGMENT,
} from './CartAddressFragment.graphql';
import {
  AVAILABLE_PAYMENT_METHOD_FRAGMENT,
  SELECTED_PAYMENT_METHOD_FRAGMENT,
} from './CartPaymentMethodFragment.graphql';

export const CHECKOUT_DATA_FRAGMENT = /* GraphQL */ `
  fragment CHECKOUT_DATA_FRAGMENT on Cart {
    id
    is_virtual
    email
    total_quantity
    billing_address {
      ...BILLING_CART_ADDRESS_FRAGMENT
    }
    shipping_addresses {
      ...SHIPPING_CART_ADDRESS_FRAGMENT
    }
    available_payment_methods {
      ...AVAILABLE_PAYMENT_METHOD_FRAGMENT
    }
    selected_payment_method {
      ...SELECTED_PAYMENT_METHOD_FRAGMENT
    }
  }

  ${BILLING_CART_ADDRESS_FRAGMENT}
  ${SHIPPING_CART_ADDRESS_FRAGMENT}
  ${AVAILABLE_PAYMENT_METHOD_FRAGMENT}
  ${SELECTED_PAYMENT_METHOD_FRAGMENT}
`;
